<template>
    <div>
      <swipe :speed="100" :defaultIndex="2" class="my-swipe">
        <swipe-item class="slide1"></swipe-item>
        <swipe-item class="slide2"></swipe-item>
        <swipe-item class="slide3"></swipe-item>
      </swipe>
      <!-- <ul id="day" v-for="(item,index) in Day" :key="index">
        <li>{{item.week}}  {{item.date}}  {{item.temperature}}  {{item.weather}}  {{item.wind}}</li>
      </ul> -->
      <ul  id='news' v-for="(item,index) in List" :key="index">
        <li class="left"><img :src="item.thumbnail_pic_s03"></li>
        <li>{{item.title}}</li>
        <li>{{item.date}}</li>
        </ul>
    </div>
</template>
<script>
require('vue-swipe/dist/vue-swipe.css');
import { Swipe, SwipeItem } from 'vue-swipe';
export default {
  components:{
    'swipe': Swipe,
    'swipe-item': SwipeItem
  },
  created(){
    let obj = {
      title:"书籍",
      className:"book"
    }
    this.$emit("changeNav",obj);
    this.getNews();
    this.getDay();
  },
  data(){
    return{
      List:[],
      Day:[],
    }
  },
  methods: {
    getDay(){
      let proxy='https://bird.ioliu.cn/v2?url=';
      let url='http://v.juhe.cn/weather/index?format=2&cityname=%E8%8B%8F%E5%B7%9E&key=b715aa32e005994cff750e415ab3bf5d';
      axios.get(proxy+url).then((res)=>{
        this.Day=res.data.result.future;
      }).catch((res) => {
        console.log('失败');
      })
    },
    getNews(){
      let proxy='https://bird.ioliu.cn/v2?url=';
      let url='http://v.juhe.cn/toutiao/index?type=top&key=38f2f3135dfa2ee953c7f2ace2cd6b4c';
      axios.get(proxy+url).then((res)=>{
        console.log(res);
        console.log(res.data.result.data);
        this.List=res.data.result.data;
      }).catch((res) => {
        console.log('失败');
      })
    },
  },
}
</script>
<style scoped>
.my-swipe {
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  background-image: url('../../assets/img/01.jpg');
  background-color: #0089dc;
  color: #fff;
}

.slide2 {
  background-image: url('../../assets/img/02.jpg');
  background-color: #ffd705;
  color: #000;
}

.slide3 {
  background-image: url('../../assets/img/03.jpg');
  background-color: #ff2d4b;
  color: #fff;
}
#news{
  width: 100%;
  height: 2rem;
}
#news .left{
    width: 40%;
    height: 100%;
    float: left;
}
#news .right{
    text-align: center;
    margin: auto;
    font-family: 'Courier New', Courier, monospace;
}
</style>
